import React from 'react';
import { Route, Routes } from 'react-router-dom';

const Setup = React.lazy(() => import('./SetupWallet/SetupWallet'));
const SetupOptions = React.lazy(() => import('./SetupOptions/SetupOptions'));
const AccountType = React.lazy(() => import('./AccountType/AccountType'));
const AccountReady = React.lazy(() => import('./AccountReady/AccountReady'));
const AccountList = React.lazy(() => import('./List/List'));
const AccountListByBusiness = React.lazy(() => import('./List/ListByBusiness'));

const AccountRouter: React.FC = () => (
  <Routes>
    <Route path="setup-options" element={<SetupOptions />} />
    <Route path="account-type" element={<AccountType />} />
    <Route path="setup" element={<Setup />} />
    <Route path="ready" element={<AccountReady />} />
    <Route path="list" element={<AccountList />} />
    <Route path="list-by-business/:businessId" element={<AccountListByBusiness />} />
  </Routes>
);

export default AccountRouter;
